/* Course Detail Page Styles */

.course-detail-container {
    min-height: calc(100vh - 140px);
    background: linear-gradient(135deg, rgba(29, 126, 153, 0.05) 0%, rgba(0, 0, 0, 0.8) 100%);
}

.course-detail-header {
    background: rgba(29, 126, 153, 0.1);
    border-bottom: 1px solid rgba(29, 126, 153, 0.2);
    padding: 40px 20px;
}

.course-hero {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 40px;
}

.course-hero-content {
    display: flex;
    gap: 30px;
    flex: 1;
    align-items: flex-start;
}

.course-icon-large {
    font-size: 4rem;
    background: var(--metallic-gradient);
    width: 100px;
    height: 100px;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    flex-shrink: 0;
}

.course-info {
    flex: 1;
}

.course-detail-title {
    font-size: 2.5rem;
    font-weight: bold;
    color: var(--text-color);
    margin-bottom: 15px;
    line-height: 1.2;
}

.course-detail-description {
    color: var(--secondary-text-color);
    font-size: 1.1rem;
    line-height: 1.6;
    margin-bottom: 20px;
}

.course-meta-detail {
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
}

.course-level-detail {
    background: rgba(29, 126, 153, 0.2);
    color: var(--primary-color);
    padding: 6px 15px;
    border-radius: 20px;
    font-size: 0.9rem;
    font-weight: 500;
    border: 1px solid rgba(29, 126, 153, 0.3);
}

.course-duration-detail,
.course-rating-detail {
    color: var(--secondary-text-color);
    font-size: 0.95rem;
    display: flex;
    align-items: center;
}

.course-enrollment {
    background: rgba(29, 126, 153, 0.1);
    border: 1px solid rgba(29, 126, 153, 0.2);
    border-radius: 12px;
    padding: 25px;
    text-align: center;
    min-width: 200px;
}

.course-price-detail {
    margin-bottom: 20px;
}

.original-price-detail {
    text-decoration: line-through;
    color: var(--secondary-text-color);
    font-size: 1rem;
    margin-right: 10px;
}

.current-price-detail {
    font-size: 1.8rem;
    font-weight: bold;
    color: var(--primary-color);
}

.enroll-btn-detail {
    width: 100%;
    background: var(--metallic-gradient);
    color: white;
    border: none;
    padding: 15px 25px;
    border-radius: 8px;
    font-weight: 600;
    font-size: 1.1rem;
    cursor: pointer;
    transition: all 0.3s ease;
}

.enroll-btn-detail:hover {
    background: var(--metallic-gradient-reverse);
    transform: translateY(-2px);
    box-shadow: 0 8px 20px rgba(29, 126, 153, 0.3);
}

.course-content {
    max-width: 1200px;
    margin: 0 auto;
    padding: 40px 20px;
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 40px;
}

.course-main {
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.course-sidebar {
    display: flex;
    flex-direction: column;
    gap: 25px;
}

.course-section {
    background: rgba(29, 126, 153, 0.1);
    border: 1px solid rgba(29, 126, 153, 0.2);
    border-radius: 12px;
    padding: 25px;
}

.course-section h2 {
    color: var(--text-color);
    font-size: 1.5rem;
    margin-bottom: 20px;
    background: var(--metallic-gradient);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.course-section h3 {
    color: var(--text-color);
    font-size: 1.2rem;
    margin-bottom: 15px;
    background: var(--metallic-gradient);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.outcomes-list {
    list-style: none;
    padding: 0;
}

.outcomes-list li {
    color: var(--secondary-text-color);
    padding: 8px 0;
    border-bottom: 1px solid rgba(29, 126, 153, 0.1);
    position: relative;
    padding-left: 25px;
}

.outcomes-list li:before {
    content: "✓";
    color: var(--primary-color);
    font-weight: bold;
    position: absolute;
    left: 0;
}

.outcomes-list li:last-child {
    border-bottom: none;
}

.curriculum-list {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.curriculum-item {
    background: rgba(29, 126, 153, 0.05);
    border: 1px solid rgba(29, 126, 153, 0.15);
    border-radius: 8px;
    padding: 15px;
    transition: all 0.3s ease;
}

.curriculum-item:hover {
    background: rgba(29, 126, 153, 0.1);
    border-color: rgba(29, 126, 153, 0.3);
}

.week-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.week-header h3 {
    color: var(--text-color);
    font-size: 1.1rem;
    margin: 0;
}

.lesson-count {
    color: var(--secondary-text-color);
    font-size: 0.9rem;
    background: rgba(29, 126, 153, 0.2);
    padding: 4px 10px;
    border-radius: 12px;
}

.prerequisites-list {
    list-style: none;
    padding: 0;
}

.prerequisites-list li {
    color: var(--secondary-text-color);
    padding: 5px 0;
    position: relative;
    padding-left: 20px;
}

.prerequisites-list li:before {
    content: "•";
    color: var(--primary-color);
    position: absolute;
    left: 0;
}

.instructor-info h4 {
    color: var(--text-color);
    margin-bottom: 10px;
    font-size: 1.1rem;
}

.instructor-info p {
    color: var(--secondary-text-color);
    font-size: 0.95rem;
    line-height: 1.5;
    margin: 0;
}

/* N/A Message Styling */
.na-message {
    padding: 20px;
    background: rgba(29, 126, 153, 0.05);
    border: 1px dashed rgba(29, 126, 153, 0.3);
    border-radius: 8px;
    text-align: center;
    color: var(--secondary-text-color);
    font-style: italic;
}

.na-message p {
    margin: 5px 0;
    line-height: 1.5;
}

/* Course Additional Information */
.course-additional-info {
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.info-item {
    display: flex;
    justify-content: space-between;
    padding: 8px 0;
    border-bottom: 1px solid rgba(29, 126, 153, 0.1);
    color: var(--secondary-text-color);
    font-size: 0.9rem;
}

.info-item:last-child {
    border-bottom: none;
}

.info-item strong {
    color: var(--text-color);
    font-weight: 600;
}

/* Back Button */
.back-btn {
    background: transparent;
    color: var(--primary-color);
    border: 1px solid var(--primary-color);
    padding: 10px 20px;
    border-radius: 6px;
    cursor: pointer;
    font-size: 0.9rem;
    margin-bottom: 20px;
    transition: all 0.3s ease;
    display: inline-flex;
    align-items: center;
    gap: 8px;
}

.back-btn:hover {
    background: var(--primary-color);
    color: white;
    transform: translateY(-2px);
}

/* Responsive Design */
@media (max-width: 768px) {
    .course-detail-header {
        padding: 30px 15px;
    }

    .course-hero {
        flex-direction: column;
        gap: 25px;
    }

    .course-hero-content {
        flex-direction: column;
        gap: 20px;
        text-align: center;
    }

    .course-icon-large {
        font-size: 3rem;
        width: 80px;
        height: 80px;
        margin: 0 auto;
    }

    .course-detail-title {
        font-size: 2rem;
    }

    .course-meta-detail {
        justify-content: center;
        gap: 15px;
    }

    .course-enrollment {
        width: 100%;
        max-width: 300px;
        margin: 0 auto;
    }

    .course-content {
        grid-template-columns: 1fr;
        gap: 25px;
        padding: 30px 15px;
    }

    .course-section {
        padding: 20px;
    }
}

@media (max-width: 480px) {
    .course-detail-header {
        padding: 20px 10px;
    }

    .course-detail-title {
        font-size: 1.6rem;
    }

    .course-detail-description {
        font-size: 1rem;
    }

    .course-meta-detail {
        flex-direction: column;
        align-items: center;
        gap: 10px;
    }

    .course-content {
        padding: 20px 10px;
    }

    .course-section {
        padding: 15px;
    }

    .week-header {
        flex-direction: column;
        align-items: flex-start;
        gap: 5px;
    }
}